  .header {
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
  }
}

.header .container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 6px;
}
