  .header__logo-image {
    max-width: 35px;
    max-height: 35px;
  }
}

.header__link {
  display: flex;
  align-items: center;
  font-family: "PT Mono", "Courier", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #f2f5f6;
  padding: 10px;
