    padding-bottom: 14px;
    margin-right: 20px;
  }
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__link.focus-visible:focus {
  opacity: 0.8;
}

.header__link span {
  display: inline-block;
