  .advantages__icon {
    width: 30px;
    height: 30px;
  }
}

@media (max-width: 1023px) {
  .advantages__item:last-of-type .advantages__icon {
    margin-bottom: 6px;
  }
}
@media (max-width: 767px) {
  .advantages__item:last-of-type .advantages__icon {
    margin-bottom: 11px;
  }
}

.advantages__item h3 {
  margin: 0;
