.advantages {
  background-color: $color-alice-blue2;
}

.advantages .container {
  padding-top: 122px;
  padding-bottom: 103px;

  @media (max-width: $vp-1023) {
    padding-top: 80px;
    padding-bottom: 75px;
  }

  @media (max-width: $vp-767) {
    padding-top: 50px;
    padding-bottom: 44px;
  }
}

.advantages__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 30px 32px;

  @media (max-width: 1023px) {
    gap: 30px 29px;
    padding: 0;
  }

  @media (max-width: 767px) {
    grid-template-columns: none;
    row-gap: 25px;
  }
}

.advantages__item {
  position: relative;
  border: 1px solid $color-whisper;
  box-sizing: border-box;
  padding: 37px 45px 35px;
  min-height: 300px;

  @media (max-width: $vp-1023) {
    padding: 39px 30px 28px 44px;
    min-height: 360px;
  }

  @media (max-width: $vp-767) {
    padding: 31px 29px 62px;
    min-height: 320px;
  }

  &:hover,
  &:hover .advantages__item-title,
  &:hover .advantages__description,
  &:hover .advantages__icon {
    background-color: $color-deepsky-blue1;
    color: $color-alice-blue3;
    border-color: $color-deepsky-blue1;
    fill: $color-alice-blue3;
  }

  &::before {
    position: absolute;
    content: "";
    height: 100.7%;
    width: 5px;
    top: -1px;
    left: -1px;
    background-color: $color-deepsky-blue1;
  }
}

