.advantages__icon {
  fill: $color-deepsky-blue1;
  margin-bottom: 18px;
  width: 35px;
  height: 35px;

  @media (max-width: $vp-1023) {
    margin-bottom: 12px;
  }

  @media (max-width: $vp-767) {
    width: 30px;
    height: 30px;
  }
}

.advantages__item:last-of-type .advantages__icon {
  @media (max-width: $vp-1023) {
    margin-bottom: 6px;
  }

  @media (max-width: $vp-767) {
    margin-bottom: 11px;
  }
}

.advantages__item h3 {
  margin: 0;
  margin-bottom: 14px;
  font-family: $ff-roboto;
  font-weight: 400;
  line-height: 1.4;
  color: $color-deepsky-blue1;
  font-size: 24px;

  @media (max-width: $vp-1023) {
    margin-bottom: 7px;
  }

  @media (max-width: $vp-767) {
    font-size: 20px;
  }
}

.advantages__item:last-of-type h3 {
  @media (max-width: $vp-767) {
    margin-bottom: 14px;
  }
}

.advantages__item p {
  margin: 0;
  font-family: $ff-ptmono;
  font-weight: 400;
  line-height: 1.35;
  color: $color-black-pearl1;
  font-size: 16px;
  min-height: 120px;

  @media (max-width: $vp-767) {
    font-size: 14px;
  }
}
